import avatar from "@/components/yq-avatar/yq-avatar";
export default {
	data() {
		return {
			urls: ["./../../../static/uni.png","./../../../static/uni.png"],
		}
	},
	methods: {
		doBefore() {
			console.log('doBefore');
		},
		clk(index) {
			this.$refs.avatar.fChooseImg(index,{
				selWidth: '350upx', selHeight: '350upx', 
				expWidth: '260upx', expHeight: '260upx',
				inner: index ? 'true' : 'false'
			});
		},
		doUpload(rsp) {
			console.log(rsp);
			this.$set(this.urls, rsp.index, rsp.path);
			return;
			uni.uploadFile({
				url: '', //仅为示例，非真实的接口地址
				filePath: rsp.path,
				name: 'avatar',
				formData: {
					'avatar': rsp.path
				},
				success: (uploadFileRes) => {
					console.log(uploadFileRes.data);
				},
				complete(res) {
					console.log(res)
				}
			});
		}
	},
	components: {
		avatar
	}
}
